@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.eventBtn,
.resetBtn {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  background-color: #E4C1AE;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  width: 128px;
  height: 48px;
  margin-top: 32px;
  transition: transform 0.15s ease-in-out;
}
.eventBtn:hover,
.resetBtn:hover {
  cursor: pointer;
  transform: scale(1.15);
}

@media (max-width: 986px) {
  .eventBtn {
    position: relative;
    left: 50%;
    transition: none;
    transform: translateX(-50%);
  }
  .eventBtn:hover {
    transform: translateX(-50%) scale(1);
  }
}

.resetBtn {
  visibility: hidden;
  position: absolute;
  left: 60%;
  top: 25%;
}
@media (max-width: 1086px) {
  .resetBtn {
    left: 65%;
  }
}
@media (max-width: 986px) {
  .resetBtn {
    transform: translateX(-50%);
    transition: none;
    left: 50%;
    top: 50%;
  }
  .resetBtn:hover {
    transform: translateX(-50%) scale(1);
  }
}

.closeBtn {
  cursor: pointer;
  position: absolute;
  right: 70px;
  top: 46px;
  width: 24px;
  height: 24px;
}
.closeBtn:after, .closeBtn:before {
  content: "";
  height: 24px;
  left: 11px;
  border-left: 2px solid #000;
  position: absolute;
}
.closeBtn:after {
  transform: rotate(45deg);
}
.closeBtn:before {
  transform: rotate(-45deg);
}
@media (max-width: 986px) {
  .closeBtn {
    right: 40px;
    top: 35px;
  }
}

.main-module {
  background-color: #fff;
  width: 1440px;
  position: relative;
  top: 10vh;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-wrap: wrap;
  gap: 95px;
  justify-content: center;
}
.main-module .image {
  width: 505px;
  height: 330px;
}
@media (max-width: 1086px) {
  .main-module .image {
    width: 450px;
    height: 300px;
  }
}
@media (max-width: 986px) {
  .main-module .image {
    width: 600px;
    height: 400px;
  }
}
@media (max-width: 680px) {
  .main-module .image {
    width: 450px;
    height: 300px;
  }
}
@media (max-width: 512px) {
  .main-module .image {
    width: 300px;
    height: 200px;
  }
}
@media (max-width: 360px) {
  .main-module .image {
    width: 225px;
    height: 150px;
  }
}
.main-module .content {
  display: flex;
  flex-direction: column;
  width: 380px;
  height: 250px;
}
.main-module .content .title {
  margin-top: 40px;
  font-size: 42px;
  line-height: 50px;
  font-weight: 700;
}
@media (max-width: 986px) {
  .main-module .content .title {
    font-size: 36px;
    line-height: 120%;
  }
}
@media (max-width: 512px) {
  .main-module .content .title {
    margin-top: 0;
  }
}
.main-module .content .copy {
  line-height: 150%;
  font-weight: 300;
  margin-top: 24px;
  display: flex;
  align-items: center;
}
@media (max-width: 986px) {
  .main-module .content .copy {
    font-size: 14px;
  }
}
@media (max-width: 986px) {
  .main-module .content {
    margin-bottom: 100px;
  }
}
@media (max-width: 512px) {
  .main-module .content {
    width: 80vw;
  }
}
@media (max-width: 1440px) {
  .main-module {
    width: 100vw;
  }
}
@media (max-width: 986px) {
  .main-module {
    flex-direction: column;
    align-items: center;
    gap: 35px;
    margin-bottom: 50px;
    top: 6vh;
  }
}

.popupContent {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  background-color: #fff;
  width: 63.8888888889%;
  height: 210px;
  z-index: 3;
  padding: 56px 95px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.popupContent.active {
  transform: translate(-50%, -50%) scale(1);
}
.popupContent .title {
  font-size: 42px;
  line-height: 50px;
}
@media (max-width: 986px) {
  .popupContent .title {
    font-size: 36px;
    line-height: 120%;
  }
}
.popupContent .copy {
  line-height: 24px;
}
@media (max-width: 986px) {
  .popupContent .copy {
    font-size: 14px;
  }
}
@media (max-width: 986px) {
  .popupContent {
    justify-content: normal;
    height: 240px;
    padding: 50px;
  }
}
@media (max-width: 680px) {
  .popupContent {
    width: 75%;
  }
}
@media (max-width: 360px) {
  .popupContent {
    height: 260px;
    padding: 40px;
  }
}

.popup.active {
  display: block;
}

.overlay {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.5);
  pointer-events: none;
}
.overlay.active {
  opacity: 1;
  pointer-events: all;
}

body {
  font-family: "Roboto", sans-serif;
  color: #121212;
  font-size: 16px;
}